import React from 'react'
import StartingMove from "./StartingMove"
import { v4 as uuid } from "uuid";

const StartingMoves = React.memo(function StartingMoves({ startingMoves, setStartingMoves }) {
    const addLeft = e => {
        setStartingMoves({
            ...startingMoves, 
            left: [
                ...startingMoves.left,
                {
                    title: "____",
                    text: "____",
                    id: uuid(),
                    checked: false
                }
            ]
        })
    }
    const addRight = e => {
        setStartingMoves({
            ...startingMoves, 
            right: [
                ...startingMoves.right,
                {
                    title: "____",
                    text: "____",
                    id: uuid(),
                    checked: false
                }
            ]
        })
    }
    return (
        <div className="c-span-2">
            <div className="box-dark">
                <h3 className="upper">Starting Moves</h3>
            </div>
            <div className="flex align-start">
                <ul className="flex column starting-moves-left">
                    {startingMoves.left.map((move) => {
                        return <StartingMove key={move.id} move={move} startingMoves={startingMoves} setStartingMoves={setStartingMoves} />
                    })}
                </ul>
                <ul className="flex column starting-moves-right">
                    {startingMoves.right.map((move) => {
                        return <StartingMove key={move.id} move={move} startingMoves={startingMoves} setStartingMoves={setStartingMoves} />
                    })}
                </ul>
            </div>
            <div className="flex">
                <button onClick={addLeft} className="add lock-hide">Add Row</button>
                <button onClick={addRight} className="add lock-hide">Add Row</button>
            </div>
        </div>
    )
})

export default StartingMoves
