import React from 'react'
import AlignmentItem from "./AlignmentItem"
import { v4 as uuid } from "uuid";

const Alignment = React.memo(function Alignment({ alignmentItems, setAlignmentItems }) {
    const addButtonHandler = e => {
        setAlignmentItems(items => [...items, {
            title: "___",
            text: "___",
            checked: false,
            id: uuid()
        }])
    }
    return (
        <div>
            <div className="box-dark">
                <h3 className="upper">Alignment</h3>
            </div>
            <ul className="flex column alignment">
                {alignmentItems.map(item => {
                    return (
                        <AlignmentItem key={item.id} item={item} setAlignmentItems={setAlignmentItems} alignmentItems={alignmentItems} />
                    )
                })}
            </ul>
            <button onClick={addButtonHandler} className="add lock-hide">Add Row</button>
        </div>
    )
})

export default Alignment
