import React from 'react'
import Bond from "./Bond"
import { v4 as uuid } from "uuid";

const Bonds = React.memo(function Bonds({ bondItems, setBondItems }) {
    const addButtonHandler = e => {
        setBondItems([...bondItems, {
            text: "___",
            id: uuid()
        }])
    }
    return (
        <div>
            <div className="box-dark">
                <h3 className="upper">Bonds</h3>
            </div>
            <p className="preface">Fill in at least one with the name of a companion, or write your own:</p>
            <ul className="flex column">
                {bondItems.map((item, i) => {
                    return (
                        <Bond key={item.id} item={item} setBondItems={setBondItems} bondItems={bondItems} />
                    )
                })}
            </ul>
            <button className="add lock-hide" onClick={addButtonHandler}>Add Row</button>
        </div>

    )
})

export default Bonds
