import React from 'react'
import Item from "./Item"
import { v4 as uuid } from "uuid";

const Gear = React.memo(function Gear({ strength, items, setItems, maxLoadClass, setMaxLoadClass }) {
    const addItem = e => {
        setItems([...items, {
            desc: "___",
            uses: 0,
            weight: 0,
            id: uuid()
        }])
    }

    const maxLoadClassHandler = e => {
        setMaxLoadClass(e.target.value)
    }
    return (
        <>
            <div>
                <div className="box-dark gear-head">
                    <h3 className="upper">Starting Gear</h3>
                    <p className="undertext-gear-left">Max Load (<input type="number" onChange={maxLoadClassHandler} value={maxLoadClass} className="max-load lock-disable" /> + STR)</p>
                    <p className="undertext-gear-right">Current</p>
                    <svg className="gear-bag" width="100" height="100" viewBox="0 0 100 100" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <g clipPath="url(#clip0)">
                            <path d="M87.9273 88.1652C89.3066 83.4533 91.7785 70.2149 81.6406 56.2072C75 47.0275 74.0234 53.0822 62.5 31.2072H37.5C25.9766 53.0822 25 47.0275 18.3594 56.2072C8.22754 70.2149 10.6934 83.4533 12.0758 88.1652C8.66094 89.5631 6.25 92.9139 6.25 96.8322C6.25 97.9309 6.47578 98.9746 6.82383 99.9572H93.1764C93.5242 98.9746 93.7502 97.9309 93.7502 96.8322C93.75 92.9139 91.3391 89.5631 87.9273 88.1652ZM33.6486 9.33223C31.9244 6.34161 32.9498 2.52071 35.9375 0.799616C38.9252 -0.933783 42.749 0.091608 44.4764 3.08243L44.5832 3.26563C45.6422 1.32442 47.6318 -0.0427672 50 -0.0427672C52.3682 -0.0427672 54.358 1.32442 55.4168 3.26544L55.5236 3.08223C57.251 0.0916078 61.0719 -0.933783 64.0625 0.799616C67.0533 2.5209 68.0787 6.34161 66.3514 9.33243L62.5 20.2699H37.5L33.6486 9.33223ZM65.625 28.0822C65.625 28.949 64.923 29.6447 64.0625 29.6447H35.9375C35.0738 29.6447 34.375 28.9488 34.375 28.0822C34.375 27.2156 35.0738 26.5197 35.9375 26.5197H64.0625C64.923 26.5197 65.625 27.2156 65.625 28.0822ZM65.625 23.3947C65.625 24.2615 64.923 24.9572 64.0625 24.9572H35.9375C35.0738 24.9572 34.375 24.2613 34.375 23.3947C34.375 22.5281 35.0738 21.8322 35.9375 21.8322H64.0625C64.923 21.8322 65.625 22.5281 65.625 23.3947Z" fill="black" />
                        </g>
                        <g clipPath="url(#clip0)">
                            <path d="M84.5 88.1652V82.5C84.5 76.5 85.1533 68 79 59.5C77.3531 57.2251 71.9375 51 68 49C64.0625 47 60 43.5 55.5236 37C55.5236 37 40.7084 61.5 57 80.5C65.8448 90.8152 84.5 88.1652 84.5 88.1652Z" fill="white" />
                        </g>
                    </svg>
                    <p className="load-total">{parseInt(maxLoadClass) + parseInt(strength)}</p>
                    <p className="load-current">{items.length > 0 && items.map(item => item.weight).reduce((a, b) => (parseInt(a) + parseInt(b)))}</p>

                </div>
            </div>
            <div className="desc">
                <p className="desc__desc">Item</p>
                <p className="desc__uses">Uses</p>
                <p className="desc__weight">Weight</p>
            </div>
            <ul className="inventory">
                {items.map(item => {
                    return (
                        <Item key={item.id} item={item} setItems={setItems} items={items} />
                    )
                })}
            </ul>
            <button className="add add-item lock-hide" onClick={addItem}>Add Row</button>
        </>
    )
})

export default Gear
