import React from 'react'
import AbilityScore from "./AbilityScore"

const AbilityScores = React.memo(function AbilityScores({ scores, setScores }) {
    return (
        <div className="ability-scores">
            <p className="ability-description">Assign these scores to your stats: 16(+2), 15(+1), 13(+1), 12(), 9(), 8(-1)</p>
            {scores.map((score, i) => {
                return <AbilityScore scores={scores} score={score} setScores={setScores} key={"ability-score" + i} id={i} />
            })}
        </div>
    )
})

export default AbilityScores
