import React from 'react'
import TextareaAutosize from 'react-autosize-textarea'

function StartingMove({ startingMoves, setStartingMoves, move }) {
    const changeMoveProp = (prop, newValue) => {
        setStartingMoves({
            left: startingMoves.left.map(i => {
                if (i.id === move.id) {
                    i[prop] = newValue
                }
                return i
            }),
            right: startingMoves.right.map(i => {
                if (i.id === move.id) {
                    i[prop] = newValue
                }
                return i
            })
        })
    }

    const inputTitleHandler = e => {
        changeMoveProp("title", e.target.value)
    }
    const inputTextHandler = e => {
        changeMoveProp("text", e.target.value)
    }
    const checkboxHandler = e => {
        changeMoveProp("checked", e.target.checked)
    }

    const deleteHandler = e => {
        setStartingMoves({
            left: startingMoves.left.filter(i => i.id !== move.id),
            right: startingMoves.right.filter(i => i.id !== move.id)
        })
    }


    return (
        <li className="li">
            <div className="li__top">
                <input type="checkbox" className="li__checkbox lock-disable" onChange={checkboxHandler} checked={move.checked} />
                <input type="text" className="li__title lock-disable" value={move.title} onChange={inputTitleHandler} />
            </div>
            <TextareaAutosize className="li__text li__text--p lock-disable" onChange={inputTextHandler} value={move.text} />
            <button onClick={deleteHandler} className="delete lock-hide">X</button>
        </li>
    )
}

export default StartingMove
