import React from 'react'
import Spell from "./Spell"
import { v4 as uuid } from "uuid"

function SpellLevel({ spells, setSpells, desc }) {
    const addSpellLeft = e => {
        setSpells({
            ...spells,
            left: [
                ...spells.left,
                {
                    title: "____",
                    sub: "____",
                    text: "____",
                    id: uuid(),
                    checked: false
                }
            ]
        })
    }
    const addSpellCenter = e => {
        setSpells({
            ...spells,
            center: [
                ...spells.center,
                {
                    title: "____",
                    sub: "____",
                    text: "____",
                    id: uuid(),
                    checked: false
                }
            ]
        })
    }
    const addSpellRight = e => {
        setSpells({
            ...spells,
            right: [
                ...spells.right,
                {
                    title: "____",
                    sub: "____",
                    text: "____",
                    id: uuid(),
                    checked: false
                }
            ]
        })
    }
    return (
        <div className="grid fr-3">
            <div className="box-dark c-span-3">
                <h3 className="upper">{desc}</h3>
            </div>
            <ul className="flex column">
                {spells.left.map(spell => {
                    return <Spell spell={spell} key={spell.id} spells={spells} setSpells={setSpells} />
                })}
            </ul>
            <ul className="flex column">
                {spells.center.map(spell => {
                    return <Spell spell={spell} key={spell.id} spells={spells} setSpells={setSpells} />
                })}
            </ul>
            <ul className="flex column">
                {spells.right.map(spell => {
                    return <Spell spell={spell} key={spell.id} spells={spells} setSpells={setSpells} />
                })}
            </ul>
            <button className="add lock-hide" onClick={addSpellLeft}>Add Row</button>
            <button className="add lock-hide" onClick={addSpellCenter}>Add Row</button>
            <button className="add lock-hide" onClick={addSpellRight}>Add Row</button>
        </div>
    )
}

export default SpellLevel
