import React from 'react'
import TextareaAutosize from 'react-autosize-textarea'

function Spell({ spell, spells, setSpells }) {
    const changeSpellProp = (prop, newValue) => {
        setSpells({
            left: spells.left.map(i => {
                if (i.id === spell.id) {
                    i[prop] = newValue
                }
                return i
            }),
            center: spells.center.map(i => {
                if (i.id === spell.id) {
                    i[prop] = newValue
                }
                return i
            }),
            right: spells.right.map(i => {
                if (i.id === spell.id) {
                    i[prop] = newValue
                }
                return i
            })
        })
    }

    const inputTitleHandler = e => {
        changeSpellProp("title", e.target.value)
    }

    const inputTextHandler = e => {
        changeSpellProp("text", e.target.value)
    }

    const inputSubHandler = e => {
        changeSpellProp("sub", e.target.value)
    }

    const checkboxHandler = e => {
        changeSpellProp("checked", e.target.checked)
    }

    const deleteHandler = e => {
        setSpells({
            left: spells.left.filter(i => i.id !== spell.id),
            center: spells.center.filter(i => i.id !== spell.id),
            right: spells.right.filter(i => i.id !== spell.id)
        })
    }
    return (
        <li className="li">
            <div className="li__top">
                <input type="checkbox" className="li__checkbox lock-disable" onChange={checkboxHandler} checked={spell.checked} />
                <input type="text" className="li__title lock-disable" value={spell.title} onChange={inputTitleHandler} />
            </div>
            <TextareaAutosize className="li__text li__text--p li__text--i lock-disable" onChange={inputSubHandler} value={spell.sub} />
            <TextareaAutosize className="li__text li__text--p lock-disable" onChange={inputTextHandler} value={spell.text} />
            <button onClick={deleteHandler} className="delete lock-hide">X</button>
        </li>
    )
}

export default Spell
