import React from 'react'

function AbilityScore({ scores, score, setScores, id }) {
    const getMod = (val, isDebuffOn) => {
        let result
        switch (parseInt(val)) {
            case 1:
            case 2:
            case 3:
                result = -3
                break;
            case 4:
            case 5:
                result = -2
                break;
            case 6:
            case 7:
            case 8:
                result = -1
                break;
            case 9:
            case 10:
            case 11:
            case 12:
                result = 0
                break;
            case 13:
            case 14:
            case 15:
                result = 1
                break;
            case 16:
            case 17:
                result = 2
                break;
            case 18:
                result = 3
                break;

            default:
                result = 0
                break;
        }
        if (isDebuffOn) {
            return result - 1
        } else {
            return result
        }
    }

    const valueHandler = e => {
        switch (id) {
            case 0:
                setScores(
                    [
                        {
                            ...score,
                            value: e.target.value,
                            mod: getMod(e.target.value, score.debuffOn)
                        },
                        ...scores.slice(1)
                    ]
                )

                break;

            case 5:
                setScores(
                    [
                        ...scores.slice(0, 5),
                        {
                            ...score,
                            value: e.target.value,
                            mod: getMod(e.target.value, score.debuffOn)
                        }
                    ]
                )
                break;

            default:
                setScores(
                    [
                        ...scores.slice(0, id),
                        {
                            ...score,
                            value: e.target.value,
                            mod: getMod(e.target.value, score.debuffOn)
                        },
                        ...scores.slice(id + 1, 6)
                    ]
                )
                break;
        }
    }
    const checkedHandler = e => {
        switch (id) {
            case 0:
                setScores(
                    [
                        {
                            ...score,
                            debuffOn: e.target.checked,
                            mod: getMod(score.value, e.target.checked)
                        },
                        ...scores.slice(1)
                    ]
                )

                break;

            case 5:
                setScores(
                    [
                        ...scores.slice(0, 5),
                        {
                            ...score,
                            debuffOn: e.target.checked,
                            mod: getMod(score.value, e.target.checked)
                        }
                    ]
                )
                break;

            default:
                setScores(
                    [
                        ...scores.slice(0, id),
                        {
                            ...score,
                            debuffOn: e.target.checked,
                            mod: getMod(score.value, e.target.checked)
                        },
                        ...scores.slice(id + 1, 6)
                    ]
                )
                break;
        }
    }
    return (
        <div className="box-dark ability-box">
            <div className="box-white ability-top">
                <h2>{score.title}</h2>
                <input value={score.value} onChange={valueHandler} className="ability-input lock-disable" type="number" />
            </div>
            <button className="circle-white ability">
                <h2 className="upper ability-label">{score.label}</h2>
                <p className="ability-mod">{score.mod}</p>
            </button>
            <p className="ability-condition">
                <label>{score.debuff} -1 <input type="checkbox" onChange={checkedHandler} checked={score.debuffOn} /></label>
            </p>
        </div>
    )
}

export default AbilityScore
