import React from 'react'
import SpellLevel from "./SpellLevel"

const Spells = React.memo(function Spells({
    spells0, setSpells0,
    spells1, setSpells1,
    spells3, setSpells3,
    spells5, setSpells5,
    spells7, setSpells7,
    spells9, setSpells9
}) {
    return (
        <div className="flex column c-span-3">
            <SpellLevel spells={spells0} setSpells={setSpells0} desc="Cantrips/Rotes" />
            <SpellLevel spells={spells1} setSpells={setSpells1} desc="First Level Spells" />
            <SpellLevel spells={spells3} setSpells={setSpells3} desc="Third Level Spells" />
            <SpellLevel spells={spells5} setSpells={setSpells5} desc="Fifth Level Spells" />
            <SpellLevel spells={spells7} setSpells={setSpells7} desc="Seventh Level Spells" />
            <SpellLevel spells={spells9} setSpells={setSpells9} desc="Ninth Level Spells" />
        </div>
    )
})

export default Spells
