import React, { useState, useEffect } from 'react'
import "./DW.css"

import barbarian from "./classes/Barbarian.json"
import bard from "./classes/Bard.json"
import cleric from "./classes/Cleric.json"
import druid from "./classes/Druid.json"
import fighter from "./classes/Fighter.json"
import immolator from "./classes/Immolator.json"
import paladin from "./classes/Paladin.json"
import ranger from "./classes/Ranger.json"
import thief from "./classes/Thief.json"
import wizard from "./classes/Wizard.json"

import Name from "./components/Name"
import Level from "./components/Level"
import Look from "./components/Look"
import Armor from "./components/Armor"
import HitPoints from "./components/HitPoints"
import Damage from "./components/Damage"
import Alignment from "./components/Alignment"
import AbilityScores from "./components/AbilityScores"
import Bonds from "./components/Bonds"
import StartingMoves from "./components/StartingMoves"
import Race from "./components/Race"
import Coin from "./components/Coin"
import Gear from "./components/Gear"
import AdvancedMoves from "./components/AdvancedMoves"
import Spells from "./components/Spells"

function DW() {
    const [lock, setLock] = useState(localStorage.lock == "true" ? true : false)
    const [name, setName] = useState(localStorage.name || "")
    const [level, setLevel] = useState(localStorage.level || 0)
    const [xp, setxp] = useState(localStorage.xp || 0)
    const [lookItems, setLookItems] = useState(localStorage.lookItems ? JSON.parse(localStorage.lookItems) : [])
    const [armor, setArmor] = useState(localStorage.armor || 0)
    const [maxHPClass, setMaxHPClass] = useState(localStorage.maxHPClass || "_")
    const [currentHP, setCurrentHP] = useState(localStorage.currentHP || 0)
    const [damage, setDamage] = useState(localStorage.damage || "D4")
    const [alignmentItems, setAlignmentItems] = useState(localStorage.alignmentItems ? JSON.parse(localStorage.alignmentItems) : [])
    const [scores, setScores] = useState(localStorage.scores ? JSON.parse(localStorage.scores) : [
        {
            title: "Strength",
            label: "Str",
            debuff: "Weak",
            value: 0,
            mod: 0,
            debuffOn: false
        },
        {
            title: "Dexterity",
            label: "Dex",
            debuff: "Shaky",
            value: 0,
            mod: 0,
            debuffOn: false
        },
        {
            title: "Constitution",
            label: "Con",
            debuff: "Sick",
            value: 0,
            mod: 0,
            debuffOn: false
        },
        {
            title: "Intelligence",
            label: "Int",
            debuff: "Stunned",
            value: 0,
            mod: 0,
            debuffOn: false
        },
        {
            title: "Wisdom",
            label: "Wis",
            debuff: "Confused",
            value: 0,
            mod: 0,
            debuffOn: false
        },
        {
            title: "Charisma",
            label: "Cha",
            debuff: "Scarred",
            value: 0,
            mod: 0,
            debuffOn: false
        },
    ])
    const [bondItems, setBondItems] = useState(localStorage.bondItems ? JSON.parse(localStorage.bondItems) : [])
    const [startingMoves, setStartingMoves] = useState(localStorage.startingMoves ? JSON.parse(localStorage.startingMoves) : {
        left: [],
        right: []
    })
    const [races, setRaces] = useState(localStorage.races ? JSON.parse(localStorage.races) : [])
    const [coin, setCoin] = useState(localStorage.coin || 0)
    const [items, setItems] = useState(localStorage.items ? JSON.parse(localStorage.items) : [])
    const [maxLoadClass, setMaxLoadClass] = useState(localStorage.maxLoadClass || 0)
    const [advancedMoves, setAdvancedMoves] = useState(localStorage.advancedMoves ? JSON.parse(localStorage.advancedMoves) : {
        top: {
            left: [],
            right: []
        },
        bottom: {
            left: [],
            right: []
        }
    })
    const [spells0, setSpells0] = useState(localStorage.spells0 ? JSON.parse(localStorage.spells0) : {
        left: [],
        center: [],
        right: []
    })
    const [spells1, setSpells1] = useState(localStorage.spells1 ? JSON.parse(localStorage.spells1) : {
        left: [],
        center: [],
        right: []
    })
    const [spells5, setSpells5] = useState(localStorage.spells3 ? JSON.parse(localStorage.spells3) : {
        left: [],
        center: [],
        right: []
    })
    const [spells7, setSpells7] = useState(localStorage.spells5 ? JSON.parse(localStorage.spells5) : {
        left: [],
        center: [],
        right: []
    })
    const [spells9, setSpells9] = useState(localStorage.spells7 ? JSON.parse(localStorage.spells7) : {
        left: [],
        center: [],
        right: []
    })
    const [spells3, setSpells3] = useState(localStorage.spells9 ? JSON.parse(localStorage.spells9) : {
        left: [],
        center: [],
        right: []
    })

    const [selectedClass, setSelectedClass] = useState("barbarian")

    useEffect(() => {
        localStorage.setItem("name", name)
    }, [name])
    useEffect(() => {
        localStorage.setItem("level", level)
    }, [level])
    useEffect(() => {
        localStorage.setItem("xp", xp)
    }, [xp])
    useEffect(() => {
        localStorage.setItem("lookItems", JSON.stringify(lookItems))
    }, [lookItems])
    useEffect(() => {
        localStorage.setItem("armor", armor)
    }, [armor])
    useEffect(() => {
        localStorage.setItem("maxHPClass", maxHPClass)
    }, [maxHPClass])
    useEffect(() => {
        localStorage.setItem("currentHP", currentHP)
    }, [currentHP])
    useEffect(() => {
        localStorage.setItem("alignmentItems", JSON.stringify(alignmentItems))
    }, [alignmentItems])
    useEffect(() => {
        localStorage.setItem("scores", JSON.stringify(scores))
    }, [scores])
    useEffect(() => {
        localStorage.setItem("bondItems", JSON.stringify(bondItems))
    }, [bondItems])
    useEffect(() => {
        localStorage.setItem("startingMoves", JSON.stringify(startingMoves))
    }, [startingMoves])
    useEffect(() => {
        localStorage.setItem("races", JSON.stringify(races))
    }, [races])
    useEffect(() => {
        localStorage.setItem("coin", coin)
    }, [coin])
    useEffect(() => {
        localStorage.setItem("items", JSON.stringify(items))
    }, [items])
    useEffect(() => {
        localStorage.setItem("maxLoadClass", maxLoadClass)
    }, [maxLoadClass])
    useEffect(() => {
        localStorage.setItem("advancedMoves", JSON.stringify(advancedMoves))
    }, [advancedMoves])
    useEffect(() => {
        localStorage.setItem("spells0", JSON.stringify(spells0))
    }, [spells0])
    useEffect(() => {
        localStorage.setItem("spells1", JSON.stringify(spells1))
    }, [spells1])
    useEffect(() => {
        localStorage.setItem("spells3", JSON.stringify(spells3))
    }, [spells3])
    useEffect(() => {
        localStorage.setItem("spells5", JSON.stringify(spells5))
    }, [spells5])
    useEffect(() => {
        localStorage.setItem("spells7", JSON.stringify(spells7))
    }, [spells7])
    useEffect(() => {
        localStorage.setItem("spells9", JSON.stringify(spells9))
    }, [spells9])
    useEffect(() => {
        localStorage.setItem("damage", damage)
    }, [damage])

    const downloadHandler = e => {
        const data = `text/json;charset=utf-8,${encodeURIComponent(JSON.stringify(localStorage))}`
        e.target.setAttribute("href", `data:${data}`)
    }

    const loadCharacter = (loadedCharacter) => {
        setName(loadedCharacter.name)
        setLevel(loadedCharacter.level)
        setxp(loadedCharacter.xp)
        setLookItems(JSON.parse(loadedCharacter.lookItems))
        setArmor(loadedCharacter.armor)
        setMaxHPClass(loadedCharacter.maxHPClass)
        setCurrentHP(loadedCharacter.currentHP)
        setAlignmentItems(JSON.parse(loadedCharacter.alignmentItems))
        setScores(JSON.parse(loadedCharacter.scores))
        setBondItems(JSON.parse(loadedCharacter.bondItems))
        setStartingMoves(JSON.parse(loadedCharacter.startingMoves))
        setRaces(JSON.parse(loadedCharacter.races))
        setItems(JSON.parse(loadedCharacter.items))
        setAdvancedMoves(JSON.parse(loadedCharacter.advancedMoves))
        setSpells0(JSON.parse(loadedCharacter.spells0))
        setSpells1(JSON.parse(loadedCharacter.spells1))
        setSpells3(JSON.parse(loadedCharacter.spells3))
        setSpells5(JSON.parse(loadedCharacter.spells5))
        setSpells7(JSON.parse(loadedCharacter.spells7))
        setSpells9(JSON.parse(loadedCharacter.spells9))
        setDamage(loadedCharacter.damage)
        setCoin(loadedCharacter.coin)
        setMaxLoadClass(loadedCharacter.maxLoadClass)
        setLock(localStorage.lock == "true" ? true : false)
    }

    const classes = {
        barbarian,
        bard,
        cleric,
        druid,
        fighter,
        immolator,
        paladin,
        ranger,
        thief,
        wizard
    }

    const loadClassHandler = e => {
        for (const [key, value] of Object.entries(classes)) {
            if (selectedClass == key) {
                loadCharacter(value)
            }
        }
    }

    const selectClassHandler = e => {
        setSelectedClass(e.target.value)
    }

    const uploadHandler = e => {
        const reader = new FileReader()
        const file = e.target.files[0]
        if (!file) {
            console.log("There is no file")
            return
        } else if (file.type && file.type.indexOf('application/json') === -1) {
            console.log('File is not a JSON.', file.type, file)
            return
        }
        reader.onload = ev => {
            const loadedCharacter = JSON.parse(ev.target.result)
            loadCharacter(loadedCharacter)
        }
        reader.readAsText(file)
    }

    const lockDown = checked => {
        if (checked) {
            document.querySelectorAll(".lock-hide").forEach(element => {
                element.classList.add("hide")
            })
            document.querySelectorAll(".lock-disable").forEach(element => {
                element.setAttribute("disabled", "true")
            })
            document.querySelectorAll("input[type='checkbox'].lock-disable").forEach(element => {
                !element.checked && element.closest("li").classList.add("hide")
            })
        } else {
            document.querySelectorAll(".lock-hide").forEach(element => {
                element.classList.remove("hide")
            })
            document.querySelectorAll(".lock-disable").forEach(element => {
                element.removeAttribute("disabled")
            })
            document.querySelectorAll("input[type='checkbox'].lock-disable").forEach(element => {
                !element.checked && element.closest("li").classList.remove("hide")
            })
        }
    }

    useEffect(() => {
        lockDown(lock)
        localStorage.setItem("lock", lock)
    }, [lock])

    const lockHandler = e => {
        setLock(e.target.checked)
    }

    return (
        <>
            <div className="container">
                <h1>Dungeon World Character Builder</h1>
                <p>Hello! This thing is somehow supposed to help players build their characters for their Dungeon World games online. You can load a class or type in something new. I hope this thing helps somone. Happy role-playing! </p>
                <p>You can read more about dungeon world <a href="https://dungeon-world.com/">here</a></p>
            </div>
            <div className="container">
                <select value={selectedClass} onChange={selectClassHandler}>
                    <option value="barbarian">Barbarian</option>
                    <option value="bard">Bard</option>
                    <option value="cleric">Cleric</option>
                    <option value="druid">Druid</option>
                    <option value="fighter">Fighter</option>
                    <option value="immolator">Immolator</option>
                    <option value="paladin">Paladin</option>
                    <option value="ranger">Ranger</option>
                    <option value="thief">Thief</option>
                    <option value="wizard">Wizard</option>
                </select>
                <button onClick={loadClassHandler}>Load class</button>
            </div>
            <div className="container">
                <label>Lock choices<input type="checkbox" onChange={lockHandler} checked={lock} /></label> - disable editing for most of the stuff
            </div>
            <div className="container">
                <label>Load Character(upload) 
                <input type="file" onChange={uploadHandler} /></label>
                <a onClick={downloadHandler} href="#" download={name ? name + ".json" : "character" + ".json"}>Save character(download)</a>
            </div>
            <div className="grid fr-3 container">
                <Name name={name} setName={setName} />
                <Level level={level} setLevel={setLevel} xp={xp} setxp={setxp} />
                <Look lookItems={lookItems} setLookItems={setLookItems} />
                <div className="flex column pb-2">
                    <Armor setArmor={setArmor} armor={armor} />
                    <HitPoints constitution={scores[3].value} maxHPClass={maxHPClass} setMaxHPClass={setMaxHPClass} currentHP={currentHP} setCurrentHP={setCurrentHP} />
                    <Damage damage={damage} setDamage={setDamage} />
                </div>
                <Alignment alignmentItems={alignmentItems} setAlignmentItems={setAlignmentItems} />
                <AbilityScores scores={scores} setScores={setScores} />
                <Bonds bondItems={bondItems} setBondItems={setBondItems} />
                <StartingMoves startingMoves={startingMoves} setStartingMoves={setStartingMoves} />
                <Race races={races} setRaces={setRaces} />

                <div>
                    <Coin coin={coin} setCoin={setCoin} />
                    <div>
                        <Gear strength={scores[0].mod} items={items} setItems={setItems} maxLoadClass={maxLoadClass} setMaxLoadClass={setMaxLoadClass} />
                    </div>
                </div>
                <AdvancedMoves advancedMoves={advancedMoves} setAdvancedMoves={setAdvancedMoves} />
                <Spells
                    spells0={spells0} setSpells0={setSpells0}
                    spells1={spells1} setSpells1={setSpells1}
                    spells3={spells3} setSpells3={setSpells3}
                    spells5={spells5} setSpells5={setSpells5}
                    spells7={spells7} setSpells7={setSpells7}
                    spells9={spells9} setSpells9={setSpells9}
                />
            </div>
        </>
    )
}

export default DW
