import React from 'react'
import TextareaAutosize from 'react-autosize-textarea'

function AdvancedMove({ advancedMoves, setAdvancedMoves, move }) {

    const changeMoveProp = (prop, newValue) => {
        setAdvancedMoves({
            top: {
                left: advancedMoves.top.left.map(i => {
                    if (i.id === move.id) {
                        i[prop] = newValue
                    }
                    return i
                }),
                right: advancedMoves.top.right.map(i => {
                    if (i.id === move.id) {
                        i[prop] = newValue
                    }
                    return i
                })
            },
            bottom: {
                left: advancedMoves.bottom.left.map(i => {
                    if (i.id === move.id) {
                        i[prop] = newValue
                    }
                    return i
                }),
                right: advancedMoves.bottom.right.map(i => {
                    if (i.id === move.id) {
                        i[prop] = newValue
                    }
                    return i
                })
            }
        })
    }

    const inputTitleHandler = e => {
        changeMoveProp("title", e.target.value)
    }
    const inputTextHandler = e => {
        changeMoveProp("text", e.target.value)
    }
    const checkboxHandler = e => {
        changeMoveProp("checked", e.target.checked)
    }

    const deleteHandler = e => {
        setAdvancedMoves({
            top: {
                left: advancedMoves.top.left.filter(i => i.id !== move.id),
                right: advancedMoves.top.right.filter(i => i.id !== move.id)
            },
            bottom: {
                left: advancedMoves.bottom.left.filter(i => i.id !== move.id),
                right: advancedMoves.bottom.right.filter(i => i.id !== move.id)
            }
        })
    }


    return (
        <li className="li">
            <div className="li__top">
                <input type="checkbox" className="li__checkbox lock-disable" onChange={checkboxHandler} checked={move.checked} />
                <input type="text" className="li__title lock-disable" value={move.title} onChange={inputTitleHandler} />
            </div>
            <TextareaAutosize className="li__text li__text--p lock-disable" onChange={inputTextHandler} value={move.text} />
            <button onClick={deleteHandler} className="delete lock-hide">X</button>
        </li>
    )
}

export default AdvancedMove