import React from 'react'

const Level = React.memo(function Level({ level, setLevel, xp, setxp }) {
    const handleLevelInput = (e) => {
        setLevel(e.target.value)
    }
    const handleXPInput = (e) => {
        setxp(e.target.value)
    }

    return (
        <div className="flex justify-space-between" style={{ marginTop: "-.5rem" }}>
            <label className="flex">
                <h2 className="upper">Level</h2>
                <input className="input-big circle-white level" type="number" onChange={handleLevelInput} value={level} />
            </label>
            <label className="flex box-white">
                <h2 className="upper">XP</h2>
                <input className="input-big xp" type="number" onChange={handleXPInput} value={xp} />
            </label>
        </div>
    )
})

export default Level
