import React, { useState, useEffect } from 'react'
import TextareaAutosize from 'react-autosize-textarea'

function RaceItem({ races, setRaces, item }) {

    const inputTitleHandler = e => {
        setRaces(
            races.map(i => {
                if (i.id === item.id) {
                    i.title = e.target.value
                }
                return i
            })
        )
    }
    
    const inputTextHandler = e => {
        setRaces(
            races.map(i => {
                if (i.id === item.id) {
                    i.text = e.target.value
                }
                return i
            })
        )
    }

    const checkboxHandler = e => {
        setRaces(
            races.map(i => {
                if (i.id === item.id) {
                    i.checked = e.target.checked
                }
                return i
            })
        )
    }

    const deleteHandler = e => {
        setRaces(races.filter(i => i.id !== item.id))
    }

    return (
        <li className="li">
            <div className="li__top">
                <input type="checkbox" className="li__checkbox lock-disable" checked={item.checked} onChange={checkboxHandler} />
                <input type="text" className="li__title lock-disable" value={item.title} onChange={inputTitleHandler} />
            </div>
            <TextareaAutosize className="li__text li__text--p lock-disable" onChange={inputTextHandler} value={item.text} />
            <button onClick={deleteHandler} className="delete lock-hide">X</button>
        </li>
    )
}

export default RaceItem