import React from 'react'
import TextareaAutosize from 'react-autosize-textarea'

function Item({ item, items, setItems }) {
    const changeGearProp = (prop, newValue) => {
        setItems(items.map(i => {
            if (item.id === i.id) {
                i[prop] = newValue
            }
            return i
        }))
    }

    const inputDescHandler = e => {
        changeGearProp("desc", e.target.value)
    }

    const inputUsesHandler = e => {
        changeGearProp("uses", e.target.value)
    }

    const inputWeightHandler = e => {
        changeGearProp("weight", e.target.value)
    }

    const deleteHandler = e => {
        setItems(items.filter(i => i.id !== item.id))
    }

    return (
        <li className="flex column align-start">
            <div className="item">
                <TextareaAutosize className="item__desc" onChange={inputDescHandler} value={item.desc} />
                <input className="item__uses" type="number" value={item.uses} onChange={inputUsesHandler} />
                <input className="item__weight" type="number" value={item.weight} onChange={inputWeightHandler} />
            </div>
            <button className="delete lock-hide" onClick={deleteHandler}>X</button>
        </li>
    )
}

export default Item
