import React from 'react'
import LookItem from "./LookItem"
import { v4 as uuid } from "uuid";

const Look = React.memo(function Look({ lookItems, setLookItems }) {
    const addLookItem = e => {
        setLookItems([...lookItems, {
            text: "___",
            id: uuid()
        }])
    }
    return (
        <div className="flex column align-start">
            <div className="box-dark">
                <h3 className="upper">Look</h3>
            </div>
            <p className="preface">Choose one for each or write your own:</p>
            <ul className="flex column">
                {lookItems.map((item, i) => {
                    return (
                        <LookItem key={item.id} item={item} setLookItems={setLookItems} lookItems={lookItems} />
                    )
                })}
            </ul>
            <button className="add lock-hide" onClick={addLookItem}>Add Row</button>
        </div>
    )
})

export default Look
