import React from 'react'
import AdvancedMove from "./AdvancedMove"
import { v4 as uuid } from "uuid";

const AdvancedMoves = React.memo(function AdvancedMoves({ advancedMoves, setAdvancedMoves }) {
    const addTopLeft = e => {
        setAdvancedMoves({
            ...advancedMoves,
            top: {
                ...advancedMoves.top,
                left: [
                    ...advancedMoves.top.left,
                    {
                        title: "____",
                        text: "____",
                        id: uuid(),
                        checked: false
                    }
                ]
            }
        })
    }

    const addTopRight = e => {
        setAdvancedMoves({
            ...advancedMoves,
            top: {
                ...advancedMoves.top,
                right: [
                    ...advancedMoves.top.right,
                    {
                        title: "____",
                        text: "____",
                        id: uuid(),
                        checked: false
                    }
                ]
            }
        })
    }

    const addBottomLeft = e => {
        setAdvancedMoves({
            ...advancedMoves,
            bottom: {
                ...advancedMoves.bottom,
                left: [
                    ...advancedMoves.bottom.left,
                    {
                        title: "____",
                        text: "____",
                        id: uuid(),
                        checked: false
                    }
                ]
            }
        })
    }

    const addBottomRight = e => {
        setAdvancedMoves({
            ...advancedMoves,
            bottom: {
                ...advancedMoves.bottom,
                right: [
                    ...advancedMoves.bottom.right,
                    {
                        title: "____",
                        text: "____",
                        id: uuid(),
                        checked: false
                    }
                ]
            }
        })
    }
    return (
        <div className="c-span-2">
            <div className="box-dark">
                <h3 className="upper">Advanced Moves</h3>
            </div>
            <p className="preface">When you gain a level from 2-10, you may choose from these moves.</p>
            <div className="flex align-start justify-space-between">
                <div className="flex column">
                    <ul className="flex column">
                        {advancedMoves.top.left.map(move => {
                            return <AdvancedMove key={move.id} advancedMoves={advancedMoves} setAdvancedMoves={setAdvancedMoves} move={move} />
                        })}
                    </ul>
                    <button className="add lock-hide" onClick={addTopLeft}>Add Row</button>
                </div>
                <div className="flex column">
                    <ul className="flex column">
                        {advancedMoves.top.right.map(move => {
                            return <AdvancedMove key={move.id} advancedMoves={advancedMoves} setAdvancedMoves={setAdvancedMoves} move={move} />
                        })}
                    </ul>
                    <button className="add lock-hide" onClick={addTopRight}>Add Row</button>
                </div>
            </div>
            <p className="preface">When you gain a level from 6-10, you may choose from these moves.</p>
            <div className="flex align-start justify-space-between">
                <div className="flex column">
                    <ul className="flex column">
                        {advancedMoves.bottom.left.map(move => {
                            return <AdvancedMove key={move.id} advancedMoves={advancedMoves} setAdvancedMoves={setAdvancedMoves} move={move} />
                        })}
                    </ul>
                    <button className="add lock-hide" onClick={addBottomLeft}>Add Row</button>
                </div>
                <div className="flex column">
                    <ul className="flex column">
                        {advancedMoves.bottom.right.map(move => {
                            return <AdvancedMove key={move.id} advancedMoves={advancedMoves} setAdvancedMoves={setAdvancedMoves} move={move} />
                        })}
                    </ul>
                    <button className="add lock-hide" onClick={addBottomRight}>Add Row</button>
                </div>
            </div>
        </div>
    )
})

export default AdvancedMoves
