import React from 'react'

const Name = React.memo(function Name({ name, setName }) {
    const handleNameInput = e => {
        setName(e.target.value)
    }
    return (
        <label className="c-span-2 flex box-white">
            <h2 className="upper">Name</h2>
            <input className="input-big name lock-disable" type="text" onChange={handleNameInput} value={name} />
        </label>
    )
})

export default Name
