import React from 'react'
import TextareaAutosize from 'react-autosize-textarea'

function Bond({ item, bondItems, setBondItems }) {
    const inputTextHandler = e => {
        setBondItems(
            bondItems.map(i => {
                if (i.id === item.id) {
                    i.text = e.target.value
                }
                return i
            })
        )
    }

    const deleteHandler = e => {
        setBondItems(bondItems.filter(i => i.id !== item.id))
    }
    return (
        <li className="li">
            <TextareaAutosize className="li__text lock-disable" onChange={inputTextHandler} value={item.text} />
            <button className="delete lock-hide" onClick={deleteHandler}>X</button>
        </li>
    )
}

export default Bond
