import React from 'react'
import TextareaAutosize from 'react-autosize-textarea'

function AlignmentItem({ item, alignmentItems, setAlignmentItems }) {
    const changeItemProp = (prop, newValue) => {
        setAlignmentItems(
            alignmentItems.map(i => {
                if (i.id === item.id) {
                    i[prop] = newValue
                }
                return i
            })
        )
    }

    const inputTextHandler = e => {
        changeItemProp("text", e.target.value)
    }

    const inputTitleHandler = e => {
        changeItemProp("title", e.target.value)
    }
    
    const checkboxHandler = e => {
        changeItemProp("checked", e.target.checked)
    }
    
    const deleteHandler = e => {
        setAlignmentItems(alignmentItems.filter(i => i.id !== item.id))
    }

    return (
        <li className="li">
            <div className="li__top">
                <input type="checkbox" className="li__checkbox lock-disable" onChange={checkboxHandler} checked={item.checked} />
                <input type="text" className="li__title lock-disable" value={item.title} onChange={inputTitleHandler} />
            </div>
            <TextareaAutosize className="li__text li__text--p lock-disable" onChange={inputTextHandler} value={item.text} />
            <button onClick={deleteHandler} className="delete lock-hide">X</button>
        </li>
    )
}

export default AlignmentItem
