import React from 'react'
import RaceItem from "./RaceItem"
import { v4 as uuid } from "uuid";

const Race = React.memo(function Race({ races, setRaces }) {
    const addRaceItem = e => {
        setRaces([...races, {
            title: "____",
            text: "___",
            id: uuid(),
            checked: false
        }])
    }
    return (
        <div>
            <div className="box-dark">
                <h3 className="upper">Race</h3>
            </div>
            <ul className="flex column">
                {races.map(race => {
                    return <RaceItem races={races} setRaces={setRaces} key={race.id} item={race} />
                })}
            </ul>
            <button className="add lock-hide" onClick={addRaceItem}>Add Row</button>
        </div>
    )
})

export default Race
